import PopupNavBar from '@/third/acaui/nav/popup-nav-bar.vue'
export default {
	components: {
		PopupNavBar
	},
	props: {
		value: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			show: this.value
		}
	},
	watch: {
		value(n, o) {
			this.show = n
		}
	},
	methods: {
		backEvent() {
			this.show = false
			this.$emit('input', this.show)
		},
